import Dialog from "../Dialog/Dialog";
import { hide, show } from "react-functional-modal";
import Button from "../Button/Button";
import Icon from "../Icon/Icon";
import './accountModal.less'
import WalletModal from "../WalletModal/WalletModal";
import { SUPPORTED_WALLETS,NETWORK_MAP } from "../../utils/Constants";
import { useCallback } from "react";

export default function AccountModal({walletContext,lang,alert,blockExploreUrl,openWalletModal, closeModal}){
  
  const onCopy = account => {
    if(walletContext.account){
      navigator.clipboard.writeText(account);
      alert.success(lang['account-copied'])
    }
  }

  const showWalletModal = useCallback((walletContext) => {
    openWalletModal();
    closeModal()
    // show(<WalletModal walletContext={walletContext} lang={lang}/>,{
    //   key  : 'walletModal',
    //   fading: true,
    //   style: {
    //     background: "rgba(0, 0, 0, 0.4)" ,
    //     zIndex : 2,
    //   }
    // })
  },[walletContext])
  const onDisconnect = useCallback(async () => {
    await walletContext.reset();
    // showWalletModal(walletContext)
    // onClose();
    openWalletModal();
    closeModal()
  },[walletContext])

  const changeWallet =  useCallback(() => {
    openWalletModal();
    onClose();
  },[walletContext])

  const onClose = () => {
    closeModal();
  }
  return(
    <Dialog title={lang['account']} onClose={onClose} className='account-modal'>
      <div className='account-modal-body'>
        <div className='desc'>
          {lang['connected-with']} {SUPPORTED_WALLETS[walletContext.connector] && SUPPORTED_WALLETS[walletContext.connector].name}
          <div className='chain'>
            <Icon token={`${walletContext.networkName}-logo`} width='20'/>
            {NETWORK_MAP[walletContext.networkName] || walletContext.networkName.toUpperCase()}
          </div>
        </div>
        <div className='hash-add'>
          <Icon token={walletContext.connector} width='20'/> 
          {walletContext.account}
        </div>
        <div className='anchor'>
          <div>{lang['copy-address']} <Icon token='copy'  width='12' onClick={() => onCopy(walletContext.account) }/></div>
          <div>{lang['view-on-bscscan']}<a rel='noreferrer' target='_blank' href={blockExploreUrl}><Icon token='scan' width='12'/> </a></div>
        </div>
        <div className='action'>
          <Button label={lang['disconnect']} bgColor='#203B60' outline onClick={onDisconnect}/>
          <Button label={lang['change-wallet']} bgColor='#203B60' outline onClick={changeWallet}/>
        </div>
      </div>
    </Dialog>
  )
}