import React, { useState, useEffect } from 'react'
import styled from 'styled-components';
import { inject, observer } from 'mobx-react';
import {useWallet} from 'use-wallet'
import { useRouteMatch } from 'react-router';
import { formatAddress } from '../../utils/utils';
import Button from '../Button/Button';
import Icon from '../Icon/Icon';
import { show } from 'react-functional-modal';
import WalletDialog from '../WalletModal/WalletModal';
import AccountDialog from '../AccountModal/AccountModal';
import {useAlert} from 'react-alert'
import useConfig from '../../hooks/useConfig';
import { SUPPORTED_WALLETS } from '../../utils/Constants';
import { useCallback } from 'react';
import { useModal } from 'react-hooks-use-modal';
import walletViewBg from '../../assets/img/wallet-btn-bg.png'
const Wrapper = styled.div`
  display : flex;
  align-items : center;
  color : #E0ECFF;
  cursor: pointer;
  img {
    margin-right : 8px;
  }
  .no-supported {
    white-space: nowrap;
  }
  .acc-wrapper {
    display : inherit;
  }
  &.wallet-view .acc-wrapper{
    background-color : rgba(85, 119, 253, 0.1);
    border: 1px solid #364F70;
    border-radius: 4px;
    // width: 175px;
    padding : 0 8px;
    height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &.wallet-view .acc-wrapper:hover {
    border : 1px solid #CD7A37;
    background-color : rgba(85, 119, 253, 0.1);
    background : url(${walletViewBg}) no-repeat;
  }
`


function WalletView({wallet,lang}){
  const [btnText,setBtnText] = useState(lang['connect-wallet'])
  const isIndex = useRouteMatch('/index')
  const isRoot = useRouteMatch({path: '/',exact : true})
  const isTeam = useRouteMatch('/team')
  const isDownload = useRouteMatch('/logo')
  const isInfo = useRouteMatch('/info')
  const alert = useAlert()
  const notConnectWalletPage  = isIndex || isTeam || isRoot  || isDownload || isInfo
  const walletContext = useWallet();
  
  const config = useConfig(walletContext.chainId)

  const [WalletModal, openWalletModal, closeWalletModal, isOpenWalletModal] = useModal('root', {
    preventScroll: true,
    closeOnOverlayClick: false
  });
  const [AccountModal, openAccountModal, closeAccountModal, isAccountModalOpen] = useModal('root', {
    preventScroll: true,
    closeOnOverlayClick: false
  });

  const showWalletModal = useCallback(() => {
    openWalletModal();
    // show(<WalletModal walletContext={walletContext} lang={lang}/>,{
    //   key  : 'walletModal',
    //   fading: true,
    //   style: {
    //     background: "rgba(0, 0, 0, 0.4)" ,
    //     zIndex : 2,
    //   }
    // })
  },[walletContext,lang])

  const showAccountModal = useCallback(() => {
    openAccountModal();
    // show(<AccountModal  walletContext={walletContext} lang={lang} alert={alert} blockExploreUrl={`${wallet.blockExploreUrl}address/${walletContext.account}`}/>,{
    //   key  : 'accountModal',
    //   fading: true,
    //   style: {
    //     background: "rgba(0, 0, 0, 0.4)" ,
    //     zIndex : 2
    //   }
    // })
  },[walletContext,lang])


  const setAccountText = (walletContext) => {
    //如果用户选择的网络正确
    if(walletContext.isConnected()){
      if(!walletContext.error) {
        setBtnText(<span className='address'>{formatAddress(walletContext.account)}</span>)
      } else {
        setBtnText(<span className='no-supported'>{lang['unsupported-chain-id']}{walletContext.chainId}!</span>)
      }
    } else {
      setBtnText(lang['connect-wallet'])
    } 
  }
  // useEffect(() => {
  //   const init = async () => {
  //     walletContext.connect(walletContext.connector)
  //   }
  //   if(!notConnectWalletPage){
  //     init();
  //   }
  //   return () => {}
  // }, [window.location.href])


  useEffect(() => {
    setAccountText(walletContext)
    if(walletContext.isConnected()){
      wallet.setDetail(walletContext)
      window.ethereum = walletContext.ethereum
    } else {
      wallet.setDetail({})
    }
  }, [walletContext,walletContext.account,lang]);

  return (
    <Wrapper className='wallet-view'>
      {walletContext.isConnected() ? 
        <div onClick={showAccountModal} className='acc-wrapper'>
          <Icon token={SUPPORTED_WALLETS[walletContext.connector].icon} width='20'/>
          <div onClick={wallet.connect}>{btnText}</div>
        </div>
      :
        <Button label={btnText} onClick={showWalletModal} width={176} height={32} fontWeight ={700} fontColor='#FFF' fontSize='18'/>
      }
      <WalletModal>
        <WalletDialog walletContext={walletContext} closeModal={closeWalletModal} lang={lang}/>
      </WalletModal>
      <AccountModal>
        <AccountDialog alert={alert} walletContext={walletContext} closeModal={closeAccountModal} blockExploreUrl={`${wallet.blockExploreUrl}address/${walletContext.account}`} openWalletModal={openWalletModal} lang={lang}/>
      </AccountModal>
    </Wrapper>
  )
}
export default inject('wallet')(observer(WalletView))